.growtype-search-wrapper {
  position: relative;
}
.growtype-search-wrapper .growtype-search-form {
  position: relative;
}
.growtype-search-wrapper .growtype-search-input {
  min-height: 56px;
  width: 100%;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  padding: 10px 15px;
}
.growtype-search-wrapper .growtype-search-input.is-error {
  border: 2px solid red;
}
.growtype-search-wrapper .growtype-search-inner {
  position: relative;
  padding-bottom: 20px;
  max-width: 1200px;
  margin: auto;
  padding-top: 20px;
  padding-left: 15px;
  padding-right: 15px;
}
.growtype-search-wrapper .e-label {
  color: grey;
  margin-bottom: 8px;
  font-size: 14px;
  position: absolute;
  left: 15px;
  top: 15px;
}
.growtype-search-wrapper .growtype-search-wrapper {
  text-indent: 160px;
  overflow: hidden;
  width: 40px;
  height: 30px;
  min-width: initial;
  border: none;
  padding: 0;
  border-radius: 0;
  position: relative;
}
.growtype-search-wrapper.growtype-search-fixed {
  top: 0;
  left: 0;
  right: 0;
  z-index: 999999;
  background: #fff;
  box-shadow: 1px 7px 11px 0px #f5f5f5;
  position: fixed;
  display: none;
}
.growtype-search-wrapper.growtype-search-fixed .search-main-inner {
  padding-top: 35px;
}
.growtype-search-wrapper.growtype-search-fixed #search-form {
  background: transparent;
  font-size: 22px;
  font-weight: 800;
  border: none;
  border-bottom: 1px solid #eceaea;
  box-shadow: none;
  outline: none;
  width: 100%;
  color: #333333;
  border-radius: 0;
}
.growtype-search-wrapper.growtype-search-fixed #search-form::placeholder {
  color: #767676;
}
.growtype-search-wrapper.is-loading .btn-growtype-search-submit svg:nth-child(1) {
  display: none;
}
.growtype-search-wrapper.is-loading .btn-growtype-search-submit svg:nth-child(2) {
  display: block;
}

.container .alignfull.wp-block-group:where(.has-background) .growtype-search-inner {
  padding-left: 0;
  padding-right: 0;
}

.growtype-search-results-actions {
  text-align: center;
  padding-top: 30px;
  display: none;
}

.growtype-search-results-btn {
  padding: 10px 35px;
  background: none;
  border: 1px solid black;
  border-radius: 10px;
  color: black;
}

.growtype-search-wrapper .btn-growtype-search-open {
  width: 25px;
  height: 25px;
  display: inline-block;
  cursor: pointer;
  opacity: 0.8;
}
.growtype-search-wrapper .btn-growtype-search-open svg {
  width: 100%;
  height: 100%;
}
.growtype-search-wrapper .btn-growtype-search-open:hover {
  opacity: 1;
}
.growtype-search-wrapper .btn-growtype-search-close {
  width: 20px;
  height: 20px;
  display: inline-block;
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 20px;
  transition: all 0.5s;
  opacity: 0.8;
}
.growtype-search-wrapper .btn-growtype-search-close svg {
  width: 100%;
  height: 100%;
}
.growtype-search-wrapper .btn-growtype-search-close:hover {
  opacity: 1;
  transition: all 0.5s;
}
.growtype-search-wrapper .btn-growtype-search-submit {
  position: absolute;
  bottom: 0;
  width: 40px;
  border: none;
  font-size: 28px;
  height: 39px;
  top: 8px;
  right: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: initial;
  padding: 5px;
  background-color: black;
  opacity: 0.8;
  transition: all 0.5s;
}
.growtype-search-wrapper .btn-growtype-search-submit:hover {
  opacity: 1;
  transition: all 0.5s;
}
.growtype-search-wrapper .btn-growtype-search-submit i {
  color: white;
}
.growtype-search-wrapper .btn-growtype-search-submit svg {
  width: 100%;
  height: 100%;
}
.growtype-search-wrapper .btn-growtype-search-submit svg path {
  fill: white;
}
.growtype-search-wrapper .btn-growtype-search-submit svg:nth-child(2) {
  display: none;
}
.growtype-search-wrapper .btn-growtype-search-submit svg:nth-child(1) {
  display: block;
}

.growtype-search-results {
  padding: 20px;
  background: white;
  margin-top: 10px;
  border-radius: 5px;
  overflow: scroll;
  max-height: 90vh;
  display: flex;
  flex-wrap: wrap;
}
@media only screen and (max-width: 640px) {
  .growtype-search-results {
    padding: 10px;
    margin-top: 5px;
    max-height: 82vh;
  }
}
.growtype-search-results .growtype-search-result {
  margin-top: 15px;
  display: inline-block;
  width: 100%;
  border-top: 1px solid whitesmoke;
  padding-top: 20px;
}
.growtype-search-results .growtype-search-result:first-child {
  border: none;
  margin: 0;
  padding: 0;
}
.growtype-search-results .growtype-search-result .title {
  font-size: 16px;
  padding-bottom: 5px;
  font-weight: bold;
}
.growtype-search-results .growtype-search-result .content {
  font-size: 14px;
}
.growtype-search-results .growtype-search-result .growtype-search-results-content-inner {
  display: flex;
}
.growtype-search-results .growtype-search-result .growtype-search-result-img {
  margin-right: 15px;
  width: 100%;
  max-width: 80px;
  max-height: 50px;
}
.growtype-search-results .growtype-search-result .growtype-search-result-img .img {
  width: 100%;
  height: 100%;
}
.growtype-search-results-no-results {
  padding-right: 15px;
  padding-left: 15px;
}